import React, {createContext, useState} from 'react';

import './memeCreator.css'
import DraggableLable from './DraggableLable';
import ImageTarget from './imageTarget';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';

export const LabelContex = createContext({MoveToPosition: null})

export default function MemeCreator(props) {
    //
    const [dataAndStyle,
        setDataAndStyle] = useState({
        style: {
            fontSize: "medium",
            textAlign: "center"
        },
        data: [
            {
                id: 1,
                top: 20,
                left: 20
            }
        ]
    });

    const deleteCaption = id => setDataAndStyle({
        ...dataAndStyle,
        data: dataAndStyle
            .data
            .filter(x => x.id !== id)
    });

    const addCaption = () => {
        if (dataAndStyle.data.length === 0) {
            setDataAndStyle({
                ...dataAndStyle,
                data: [
                    {
                        id: 1,
                        top: 20,
                        left: 20
                    }
                ]
            });
        } else {
            const MaxId = dataAndStyle
                .data
                .sort((x, y) => x.id<y.id
                    ? -1
                    : 1)[dataAndStyle.data.length - 1]
                .id;
            setDataAndStyle({
                ...dataAndStyle,
                data: dataAndStyle
                    .data
                    .concat({
                        id: MaxId + 1,
                        top: 0,
                        left: 0
                    })
            });
        }
    };

    const changeAlign = align => setDataAndStyle({
        ...dataAndStyle,
        style: {
            ...dataAndStyle.style,
            textAlign: align
        }
    });
    const changeSize = size => setDataAndStyle({
        ...dataAndStyle,
        style: {
            ...dataAndStyle.style,
            fontSize: size
        }
    });

    const MoveToPosition = (id, top, left) => {
        const moved = dataAndStyle
            .data
            .filter(x => x.id === id);
        moved[0].top = top;
        moved[0].left = left;
        setDataAndStyle({
            ...dataAndStyle,
            data: dataAndStyle
                .data
                .filter(x => x.id !== id)
                .concat(moved[0])
        });
    }

    const textAligns = ["left", "right", "center"];
    const textSizes = ["x-small", "small", "medium", "large", "xx-large"];

    return (
        <LabelContex.Provider value={{MoveToPosition}}>
            <div>
                <ButtonToolbar
                    style={{
                    display: "flex",
                    justifyContent: "center"}}>

                    <Button variant="success" onClick={addCaption} size="sm">
                        Add Caption
                    </Button>

                    <DropdownButton
                        as={ButtonGroup}
                        variant="success"
                        title="Text Align"
                        id="text-align-dropdown"
                        size="sm">
                        {textAligns.map((x, index) =><Dropdown.Item variant = "success" key = {
                            index
                        }
                        eventKey = {
                            index
                        }
                        onClick = {
                            () => changeAlign(x)
                        }
                        active = {
                            x === dataAndStyle.style.textAlign
                        }> {x}
                        </Dropdown.Item>)}
                    </DropdownButton>

                    <DropdownButton
                        as={ButtonGroup}
                        variant="success"
                        title="Text Size"
                        id="text-size-dropdown"
                        size="sm">
                        {textSizes.map((x, index) =><Dropdown.Item variant = "success" key = {
                            index
                        }
                        eventKey = {
                            index
                        }
                        style = {
                            {
                                fontSize: x
                            }
                        }
                        onClick = {
                            () => changeSize(x)
                        }
                        active = {
                            x === dataAndStyle.style.fontSize
                        } > {
                            x.toUpperCase()
                        }</Dropdown.Item>)}
                    </DropdownButton>

                   </ButtonToolbar>
                        <ImageTarget>
                            <img
                                src={props.url}
                                alt="meme"
                                style={{
                                display: "block",
                                maxHeight: "80%",
                                maxWidth: "80%",
                                marginLeft: "auto",
                                marginRight: "auto"
                            }}/> {dataAndStyle
                                .data
                                .map(item => <DraggableLable
                                    key={item.id}
                                    id={item.id}
                                    top={item.top}
                                    left={item.left}
                                    handleDelete=
                                    { () => deleteCaption(item.id) }
                                    styleForText={dataAndStyle.style}/>)
}
                        </ImageTarget>
                       </div>
                        </LabelContex.Provider>)}