import React, {useState} from 'react';
import axios from 'axios';
import MemeImageToSelect from './memeImageToSelect';
import MemeCreator from './memCreator';
import './content.css';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';

function Content() {
    const [memesState,
        setMemes] = useState({
        memes: [
            {
                url: "",
                height:1,
                width:1
            }
        ],
        chosen: 0,
        allMemes: [],
        numberOfMemesInPage: 10
    });

    if (memesState.memes[0].url === "") {
        console.log(memesState.memes.url)
        axios
            .get(`https://api.imgflip.com/get_memes`)
            .then(res => {
                const memes = res.data.data.memes;
                setMemes({
                    ...memesState,
                    memes: memes.slice(0, memesState.numberOfMemesInPage),
                    allMemes: memes
                });
            })
    }

    const changePicture = id => setMemes({
        ...memesState,
        chosen: id
    });
    const changeToPage = pageNum => {
        const start = pageNum * memesState.numberOfMemesInPage;
        let end = (pageNum + 1) * memesState.numberOfMemesInPage;
        end = end <memesState.allMemes.length
            ? end
            : memesState.allMemes.length;
        setMemes({
            ...memesState,
            memes: memesState
                .allMemes
                .slice(start, end),
            chosen: 0
        })
    }

    let memesMapped = memesState
        .memes
        .map((x, index) => <MemeImageToSelect
            key={x.id + memesState.numberOfMemesInPage}
            url={x.url}
            name={x.name}
            isChosen={index === memesState.chosen}
            hight={x.height}
            wid={x.width}
            Click={() => changePicture(index)}/>);

    const numberOfPages = Math.ceil(memesState.allMemes.length / memesState.numberOfMemesInPage);

    const pagingButtons = Array(numberOfPages)
        .fill()
        .map((_, i) => <Button key={i} variant="success" onClick={() => changeToPage(i)}>
            {i + 1}
        </Button>);

    const memesPerPageOptions = [5, 10, 20];

    return (
        <div className='content box'>
            <div className="middle box">
                <MemeCreator
                    key={memesState.chosen}
                    url={memesState.memes[memesState.chosen].url}/>
                </div>

                    <div className="side box">
                        <ButtonGroup
                            size="sm"
                            style={{
                            display: "flex",
                            flexWrap: "wrap"
                        }}>
                            {pagingButtons}
                            </ButtonGroup>

                                <DropdownButton
                                    as={ButtonGroup}
                                    variant="success"
                                    title="Memes per page"
                                    id="num-of-memes-dropdown"
                                    size="sm">
                                    {memesPerPageOptions.map((x, index) => 
                                        <Dropdown.Item variant = "success" key = {index}
                                    eventKey = {index}
                                    onClick = {() => setMemes({
                                            ...memesState,
                                            numberOfMemesInPage: x,
                                            chosen: 0,
                                            memes: memesState
                                                .allMemes
                                                .slice(0, x)
                                        })
                                    }
                                    active = {x === memesState.numberOfMemesInPage}>
                                {x} 
                                        </Dropdown.Item>)}
                                </DropdownButton>
                                        <br/>
                                        <br/>
                                        <ul>
                                            {memesMapped}
                                        </ul>
            </div>

        </div>
        )
    }

    export default Content;