import React, {useState} from 'react';
import {useDrag} from 'react-dnd';
import {ItemTypes} from './items';
import styled from 'styled-components';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

const StyledDiv = styled.div `
                        background-color : none;
                        font-size: 40px;
                        font-weight: bold;
                        position:absolute;
                        
    `;

function DraggableLable(props) {

    const textAreaRef = React.useRef(null);
    const dragRef = React.useRef(null);

    const [divState,
        setValue] = useState({
        value: "Type your caption here, and move it around",
        buttonVissible: false,
        textBackGround: false,
        cursorIsMove: false,
        textBoxHight: "70px",
        textBoxWidth: "250px",
        canMove: false,
        rotation: 0
    });

    const handleChange = event => setValue({
        ...divState,
        value: event.target.value
    })
    const handleClick = () => setValue({
        ...divState,
        buttonVissible: true,
        textBackGround: true
    });
    const handleLeave = () => setValue({
        ...divState,
        buttonVissible: false,
        textBackGround: false
    });
    const handleMouseUp = () => {
        if (textAreaRef.current.getBoundingClientRect().hight !== divState.textBoxHight || textAreaRef.current.getBoundingClientRect().width !== divState.textBoxwidth) {
            const hight = String(textAreaRef.current.getBoundingClientRect().height) + "px";
            const width = String(textAreaRef.current.getBoundingClientRect().width) + "px";
            setValue({
                ...divState,
                textBoxHight: hight,
                textBoxWidth: width
            })
        }

    }

    const [
        {
            isDragging
        },
        drag] = useDrag({
        item: {
            type: ItemTypes.LABEL,
            id: props.id,
            top: props.top,
            left: props.left,
            canMove: divState.canMove
        },
        collect: monitor => ({
            isDragging: !!monitor.isDragging()
        })
    })

    drag(dragRef);

    if (isDragging) 
        return (<div ref={dragRef}/>);
    
    return (
        <StyledDiv
            ref={dragRef}
            style={{
            left: props.left,
            top: props.top,
            backgroundColor: "none"
        }}
            onClick={handleClick}
            onMouseLeave={handleLeave}>
            <Button
                variant="secondary"
                size="sm"
                style={{
                position: "absolute",
                display: (divState.buttonVissible
                    ? "block"
                    : "none"),
                cursor: (divState.cursorIsMove
                    ? "move"
                    : "auto")
            }}
                onMouseEnter=
                { () => setValue({ ...divState, cursorIsMove: true, canMove: false }) }
                onMouseLeave=
                { () => setValue({ ...divState, cursorIsMove: false, canMove: false }) }
                onMouseDown=
                { () => setValue({ ...divState, canMove: true }) }
                onMouseUp=
                { () => setValue({ ...divState, canMove: false })}>
                <img src="./move-button.png" alt="" height="30px" width="25px"/>
            </Button>

            <Button
                variant="danger"
                size="sm"
                style={{
                position: "absolute",
                top: "0",
                right: "0",
                display: (divState.buttonVissible
                    ? "block"
                    : "none")
            }}
                onClick=
                { () => props.handleDelete() }>
                X
            </Button>
            <Form.Control
                ref={textAreaRef}
                as="textarea"
                value={divState.value}
                onChange={handleChange}
                onMouseUp={handleMouseUp}
                style={{
                ...props.styleForText,
                backgroundColor: (divState.textBackGround
                    ? "white"
                    : "transparent"),
                border: (divState.textBackGround
                    ? "black"
                    : "none"),
                fontWeight: "900",
                color: "white",
                textShadow: "-1px 1px 2px #000,1px 1px 2px #000,1px -1px 0 #000,-1px -1px 0 #000",
                resize: "both",
                height: divState.textBoxHight,
                width: divState.textBoxWidth
            }}/>
        </StyledDiv>
    )
}

export default DraggableLable;