import React, {useContext} from 'react';
import styled from 'styled-components';
import {useDrop} from 'react-dnd';
import {ItemTypes} from './items';
import {LabelContex} from './memCreator';

const Wrapper = styled.section `
  padding: 4em;
  position:relative;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
`;

function ImageTarget(props) {

    const {MoveToPosition} = useContext(LabelContex);

    const [,drop] = useDrop({
        accept: ItemTypes.LABEL,
        drop: (item, monitor) => {
            if (item.canMove) {
                const delta = monitor.getDifferenceFromInitialOffset();
                const left = Math.round(item.left + delta.x);
                const top = Math.round(item.top + delta.y);
                MoveToPosition(item.id, top, left);
            }
        }
    })

    

    return (
        <Wrapper ref={drop}>
            {props.children}
        </Wrapper>
    )
}

export default ImageTarget;