import React from 'react';
import ReactTooltip from "react-tooltip";
import './memeImageToSelect.css';

function MemeImageToSelect(props) {

    return (
        <li
            className={props.isChosen
            ? "active"
            : ""}
            onClick=
            { () => props.Click() }>
            <div data-tip="tooltip" data-for={props.name}>
                {props.name}
            </div>
            <ReactTooltip id={props.name} place="right" type="dark" effect="float">
                <img
                    className="toolTipImage"
                    src={props.url}
                    alt={props.name}
                    height={Math.min(props.hight, 150)}
                    width={Math.min(props.wid, 150)}/>
            </ReactTooltip>

        </li>
    )

}
export default MemeImageToSelect